"use client";
import { useRef } from "react";
import gsap from "gsap";
import { useGSAP } from "@gsap/react";
import { ScrollTrigger } from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

const portfolioItems = [
  { id: 1, src: "https://images.unsplash.com/photo-1497366216548-37526070297c?q=80&w=800", size: "col-span-2 md:col-span-1" },
  { id: 2, src: "https://images.unsplash.com/photo-1586717791821-3f44a563eb4c?q=80&w=800", size: "col-span-2 md:col-span-1" },
  { id: 3, src: "https://images.unsplash.com/photo-1512486133939-0c01b199107d?q=80&w=800", size: "col-span-2 md:col-span-1" },
  { id: 4, src: "https://images.unsplash.com/photo-1534438327276-14e5300c3a48?q=80&w=800", size: "col-span-2 md:col-span-2" },
  { id: 5, src: "https://images.unsplash.com/photo-1496181133206-80ce9b88a853?q=80&w=800", size: "col-span-2 md:col-span-1" },
];

export default function PortfolioGrid() {
  const containerRef = useRef(null);

  useGSAP(() => {
    const items = gsap.utils.toArray(".portfolio-item");

    items.forEach((item: any) => {
      // This creates the "smoothly getting bigger" effect on scroll
      gsap.fromTo(item, 
        { scale: 0.7, opacity: 0.3 }, 
        {
          scale: 1,
          opacity: 1,
          scrollTrigger: {
            trigger: item,
            start: "top bottom", // Starts when bottom of screen hits top of image
            end: "center center", // Ends when image is centered
            scrub: 1.5, // Smoothness intensity
          }
        }
      );
    });
  }, { scope: containerRef });

  return (
    <section ref={containerRef} className="max-w-7xl mx-auto px-6 py-32">
      <div className="grid grid-cols-2 md:grid-cols-3 gap-8">
        {portfolioItems.map((item) => (
          <div 
            key={item.id} 
            className={`portfolio-item overflow-hidden rounded-[2rem] bg-zinc-900 ${item.size}`}
          >
            <img 
              src={item.src} 
              className="w-full h-full object-cover grayscale hover:grayscale-0 transition-all duration-500"
              alt="Portfolio Work"
            />
          </div>
        ))}
      </div>
    </section>
  );
}